<template>
	<div class="header">
		<div class="content-wrapper">
			<div class="avatar">
				<img :src="seller.avatar">
			</div>
			<div class="content">
				<div class="title">
					<span class="brand"></span>
					<span class="name">{{seller.name}}</span>
				</div>
				<div class="description">
					{{seller.description + '/' + seller.deliveryTime + '分钟送达'}}
				</div>
				<div v-if="seller.supports" class="support"> 
					<span class="icon" :class="classMap[seller.supports[0].type]"></span>
					<span class="text">{{seller.supports[0].description}}</span>
				</div>
			</div>
			<div v-if="seller.supports" @click="showDetail()" class="support-count">
				<span class="count">{{seller.supports.length}}个</span>
				<i class="count-icon"></i>
			</div>
		</div>
		<div class="bulletin-wrapper" @click="showDetail()">
			<span class="bulletin-title"></span>
			<span class="bulletin-text">{{seller.bulletin}}</span>
		</div>
		<div class="background">
			<img :src="seller.avatar">
		</div>
		<transition name="fade">
			<div v-show="detailShow" class="detail">
				<div class="detail-wrapper  clearfix">
					<div class="detail-main">
						<h1 class="name">{{seller.name}}</h1>
						<div class="star-wrapper">
							<star :size="48" :score="seller.score"></star>
						</div>
						<div class="title">
							<div class="line"></div>
							<div class="text">优惠信息</div>
							<div class="line"></div>
						</div>
						<ul v-if="seller.supports" class="supports">
							<li class="support-item" v-for="(item,index) in seller.supports">
								<span class="icon" :class="classMap[seller.supports[index].type]"></span>
								<span class="text">{{seller.supports[index].description}}</span>
							</li>
						</ul>
						<div class="title">
							<div class="line"></div>
							<div class="text">商家公告</div>
							<div class="line"></div>
						</div>
						<div class="bulletin">
							<p class="content">{{seller.bulletin}}</p>
						</div>
					</div>
				</div>
				
				<div class="detail-close"  @click="hideDetail()">
					<i class="closesvg"></i>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
	import star from '../../components/star/star'
	export default{
		name:'header',
		props: {
			seller:{
				type: Object
			}
		},
		data(){
			return{
				detailShow: false,
			}
		},
		methods:{
			showDetail(){
				this.detailShow = true;
			},
			hideDetail(){
				this.detailShow = false;
			}
		},
		created() {
      		this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    	},
    	components:{
    		star
    	}

	}
</script>

<style lang="scss">
@mixin bg-image($url) {
    background-image: url($url + "@2x.png");
    @media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3) {
        background-image: url($url + "@3x.png");
    }
}

.header{
	color: #fff;
	background:rgba(7,17,27,.3);
	overflow: hidden;
	position:relative;
	.content-wrapper{
		padding: 24px 12px 18px 24px;
		position: relative;
		.avatar{
			display: inline-block;
			vertical-align: top;
			img{
				width: 64px;
				height: 64px;
				border-radius: 2px;
			}
		}
		.content{
			display: inline-block;
			font-size: 14px;
			margin-left: 16px;
			.title{
				margin:2px 0 8px 0;
				.brand{
					display: inline-block;
					width: 30px;
					height: 18px;
					@include bg-image('brand');
		            background-size: 30px 18px;
		            background-repeat: no-repeat;
		            vertical-align: top;
				} 
				.name{
		            margin-left: 6px;
		            font-size: 16px;
		            line-height: 18px;
		            font-weight: bold;
		        }
			}
			.description{
				margin-bottom: 10px;
				line-height: 12px;
				font-size: 12px;
			}
			.support{
				.icon{
					display: inline-block;
					vertical-align: top;
					width: 12px;
					height: 12px;
					margin-right: 4px;
					background-size: 12px 12px;
					background-repeat:no-repeat;
					&.decrease{
						@include bg-image('decrease_1');
					}
					&.discount{
						@include bg-image('discount_1');
					}
		            &.guarantee{
		            	@include bg-image('guarantee_1');
		            }
		            &.invoice{
		            	@include bg-image('invoice_1');
		            }
		            &.special{
		            	@include bg-image('special_1');
		            }

				}
				.text{
					line-height: 12px;
					font-size: 12px;
					vertical-align: top;
				}
			}
		}
		.support-count{
			position: absolute;
			right: 12px;
			bottom: 18px;
			padding: 0 8px;
			height: 24px;
			line-height: 24px;
			border-radius:14px;
			background:rgba(0,0,0,.2);
			text-align:center;
			.count{
				font-size: 10px;
				vertical-align: top;
			}
			.count-icon{
				vertical-align: middle;
				width: 12px;
				height: 12px;
				line-height: 12px;
				background-image:url('gduo.png');
				background-size:12px 12px;
				background-repeat:no-repeat;
				display: inline-block;	
			}

		}
	}
	.bulletin-wrapper{
		height: 28px;
		line-height: 28px;
		padding:0 22px 0 12px;
		white-space: nowrap;
		overflow:hidden;
		text-overflow: ellipsis; 
		background:rgba(7,17,27,.2);
		.bulletin-title{
			@include bg-image('bulletin');
			display: inline-block;
			width: 22px;
			height: 12px;
			margin-top: 8px;
			vertical-align: top;
			background-size:22px 12px;
			background-repeat:no-repeat;
		}
		.bulletin-text{
			vertical-align: top;
			font-size: 10px;
			margin: 0 4px;
		}
	}
	.background{
		position:absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
		filter: blur(7px);
		img{
			width: 100%;
			height: 100%;
		}
	}
	.detail{
		position:fixed;
		z-index: 100;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		overflow: auto;
		opacity: 1;
		backdrop-filter: blur(10px);
		background:rgba(7,17,27,.8);
		&.fade-enter-active,&.fade-leave-active{
			transition: all 0.5s
		}
		&.fade-enter,&.fade-leave{
			opacity: 0;
			background:rgba(7,17,27,0);
		}
		.detail-wrapper{
			width: 100%;
			min-height: 100%;
			.detail-main{
				margin-top: 64px;
				padding-bottom: 64px;
				.name{
					line-height: 16px;
					text-align: center;
					font-size: 16px;
					font-weight: 700;
				}
				.star-wrapper{
					margin-top: 18px;
					padding:2px 0;
					text-align: center;
				}
				.title{
					width: 80%;
					margin: 30px auto 24px auto;
					display: flex;
					.line{
						flex: 1;
						position:relative;
						top: -6px;
						border-bottom: 1px solid rgba(255,255,255,.2)
					}
					.text{
						padding:0 12px;
						font-size: 14px;
						font-weight: 700;
					}
				}
				.supports{
					width: 80%;
					margin: 0 auto;
					.support-item{
						padding: 0 12px;
						margin-bottom: 12px;
						font-size: 0;
						&:last-child{
							margin-bottom: 0;
						}
						.icon{
							display: inline-block;
							width: 16px;
							height: 16px;
							vertical-align: top;
							margin-right: 6px;
							background-size:16px 16px;
							background-repeat: no-repeat;
							&.decrease{
								@include bg-image('decrease_2');
							}
							&.discount{
								@include bg-image('discount_2');
							}
				            &.guarantee{
				            	@include bg-image('guarantee_2');
				            }
				            &.invoice{
				            	@include bg-image('invoice_2');
				            }
				            &.special{
				            	@include bg-image('special_2');
				            }
						}
						.text{
							line-height: 16px;
							font-size: 12px;

						}
					}
				}
				.bulletin{
					width: 80%;
					margin: 0 auto;
					.content{
						padding:0 12px;
						line-height: 24px;
						font-size: 12px;
					}
				}
				
			}
		}
		.detail-close{
			position:relative;
			width: 32px;
			height: 32px;
			margin: -64px auto 0 auto;
			clear: both;
			font-size: 32px;
			.closesvg{
				width: 30px;
				height: 30px;
				background-image:url('close.png');
				background-size:30px 30px;
				position:absolute;
			}
		}
	}
}
</style>7