<template>
	<div class="seller" ref="seller">
		<div class="seller-content">
			<div class="overview">
				<h1 class="title">{{seller.name}}</h1>
				<div class="desc">
					<star :size="36" :score="seller.score"></star>
					<span class="text">({{seller.ratingCount}})</span>
					<span class="text">月售{{seller.sellCount}}单</span>
				</div>
				<ul class="remark">
					<li class="block">
						<h2>起送价</h2>
						<div class="content">
							<span class="stress">{{seller.minPrice}}</span>
						</div>
					</li>
					<li class="block">
						<h2>商家配送</h2>
						<div class="content">
							<span class="stress">{{seller.deliveryPrice}}</span>
							元
						</div>
					</li>
					<li class="block">
						<h2>平均配送时间</h2>
						<div class="content">
							<span class="stress">{{seller.deliveryTime}}</span>
							分钟
						</div>
					</li>
				</ul>
				<div class="favorite" @click="toggleFavorite">
					<span class="icon-favorite" :class="{'active':favorite}"></span>
					<span class="text">{{favoriteText}}</span>
				</div>
				
			</div>
			<split></split>
			<div class="bulletin">
				<h1 class="title">公告与活动</h1>
				<div class="content-wrapper">
					<p class="content">{{seller.bulletin}}</p>
				</div>
				<ul v-if="seller.supports" class="supports">
					<li class="support-item" v-for="(item,index) in seller.supports">
						<span class="icon" :class="classMap[seller.supports[index].type]"></span>
						<span class="text">{{seller.supports[index].description}}</span>
					</li>
				</ul>
			</div>
			<split></split>
			<div class="pics">
				<h1 class="title">商家实景</h1>
				<div class="pic-wrapper" ref="picwrapper">
					<ul class="pic-list" ref="piclist">
						<li class="pic-item" v-for="pic in seller.pics">
							<img :src="pic">
						</li>
					</ul>
				</div>
			</div>
			<split></split>
			<div class="info">
				<h1 class="title">商家信息</h1>
				<ul>
					<li class="info-item" v-for="info in seller.infos">{{info}}</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
import BScroll from 'better-scroll'; 
import star from '../star/star.vue';
import split from '../split/split.vue';
import {saveToLocal, loadFromLocal} from '../../assets/js/store.js';

	export default{
		name:'seller',
		props: {
			seller:{
				type:Object
			}
		},
		data(){
			return{
				favorite: (() => {
		          return loadFromLocal(this.seller.id, 'favorite', false);
		        })()
			}
		},
		computed: {
	      favoriteText() {
	        return this.favorite ? '已收藏' : '收藏';
	      }
	    },
		methods:{
			_initScroll(){
				if (!this.scroll) {
					this.scroll = new BScroll(this.$refs.seller,{
						click: true
					});
				}else{
					this.scroll.refresh();
				}
			},
			_initPics(){
				if (this.seller.pics) {
				let picWidth = 120;
				let margin = 6;
				let width = (picWidth + margin) * this.seller.pics.length  - margin;
				this.$refs.piclist.style.width = width + 'px';
				this.$nextTick(() =>{
					if (!this.picscroll) {
						this.picScroll = new BScroll(this.$refs.picwrapper,{
							scrollX:true,
							eventPassthrough: 'vertical'
						})
					}else{
						this.scroll.refresh();
					}
				});
			}
			},
			toggleFavorite(){
				this.favorite = !this.favorite;
				saveToLocal(this.seller.id, 'favorite', this.favorite);
			}
		},
		mounted() {
		    this.$nextTick(() => {
		       this._initScroll();
		       this._initPics();
		    });
   		},
		created(){
			this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
		},
		watch: {
			'seller'(){
				this.$nextTick(() => {
		          this._initScroll();
		          this._initPics();
		        });
			}
		},
		components:{
			star,
			split
		}
	}
</script>

<style lang="scss">
@mixin bg-image($url) {
    background-image: url($url + "@2x.png");
    @media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3) {
        background-image: url($url + "@3x.png");
    }
}


.seller{
	position: absolute;
	top: 174px;
	bottom: 0;
	left: 0;
	width: 100%;
	overflow: hidden;
	.overview{
		position:relative;
		padding: 18px;
		.title{
			margin-bottom: 8px;
			line-height: 14px;
			color: rgb(7,17,27);
			font-size: 14px;
		}
		.desc{
			padding-bottom: 18px;
			border-bottom:1px solid rgba(7,17,27,.05);
			.star{
				display: inline-block;
				margin-right: 8px;
				vertical-align: top;
			}
			.text{
				display: inline-block;
				margin-right: 12px;
				line-height: 18px;
				vertical-align: top;
				font-size: 10px;
				color: rgb(77,85,93);
			}

		}
		.remark{
			display: flex;
			padding-top: 18px;
			.block{
				flex: 1;
				text-align: center;
				border-right:1px solid rgba(7,17,27,.1);
				&:last-child{
					border:none;
				}
				h2{
					margin-bottom: 4px;
					line-height: 10px;
					font-size: 10px;
					color: rgb(147,153,159);
				}
				.content{
					line-height: 24px;
					font-size: 10px;
					color: rgb(7,17,27);
					.stress{
						font-size: 24px;
					}
				}
			}
		}
		.favorite{
			position:absolute;
			width: 50px;
			right: 11px;
			top: 18px;
			text-align: center;
			.icon-favorite{
				display: block;
				margin-left: 13px;
				margin-bottom: 4px;
				width: 24px;
				height: 24px;
				background-size:24px 24px;
				line-height: 24px;
				background-image:url('1.png');
				&.active{
					background-image:url('0.png');
				}
			}
			.text{
				line-height: 10px;
				font-size: 10px;
				color: rgb(77,85,93);
			}
		}

	}
	.bulletin{
		padding:18px 18px 0 18px;
		.title{
			margin-bottom: 8px;
			line-height: 14px;
			color: rgb(7,17,27);
			font-size: 14px;
		}
		.content-wrapper{
			padding:0 12px 16px 12px;
			border-bottom:1px solid rgba(7,17,27,.05);
			.content{
				line-height: 24px;
				font-size: 12px;
				color: rgb(240,20,20);
			}
		}
		.supports{
			.support-item{
				padding:16px 12px;
				border-bottom:1px solid rgba(7,17,27,.05);
				&:last-child{
					border:none;
				}
			}
			.icon{
				display: inline-block;
				width: 16px;
				height: 16px;
				vertical-align: top;
				margin-right: 6px;
				background-size:16px 16px;
				background-repeat:no-repeat;
				&.decrease{
					@include bg-image('decrease_4');
				}
				&.discount{
					@include bg-image('discount_4');
				}
		        &.guarantee{
		            @include bg-image('guarantee_4');
		        }
		           &.invoice{
		            @include bg-image('invoice_4');
		        }
		        &.special{
		            @include bg-image('special_4');
		        }
			}
			.tetx{
				line-height: 16px;
				font-size: 12px;
				color: rgb(7,17,27);
			}

		}
	}
	.pics{
		padding:18px;
		.title{
			margin-bottom: 12px;
			line-height: 14px;
			color: rgb(7,17,27);
			font-size: 14px;
		}
		.pic-wrapper{
			width: 100%;
			overflow: hidden;
			white-space: nowrap;
			.pic-list{
				.pic-item{
					display: inline-block;
					margin-right: 6px;
					width: 120px;
					height: 90px;
					img{
						width: 120px;
						height: 90px;
					}
				}
			}
		}
	}
	.info{
		padding: 18px 18px 0 18px;
		color: rgb(7,17,27);
		.title{
			padding-bottom:12px;
			line-height: 14px;
			border-bottom:1px solid rgba(7,17,27,.05);
			font-size: 14px;
		}
		.info-item{
			padding:16px 12px;
			line-height: 16px;
			border-bottom:1px solid rgba(7,17,27,.05);
			font-size: 12px;
			&:last-child{
				border:none;
			}
		}
	}
}
</style>