<template>
	<div>
		<transition name="move">
			<div v-show="showFlag" class="food" ref="food">
				<div class="food-content">
					<div class="image-header">
						<img :src="food.image">
						<div class="back" @click="hide">
							<i class="comeBack"></i>
						</div>
					</div>
					<div class="content">
						<h1 class="title">{{food.name}}</h1>
						<div class="detail">
							<span class="sell-count">月售{{food.sellCount}}份</span>
							<span class="rating">好评率{{food.rating}}%</span>
						</div>
						<div class="price">
							<span class="now">￥{{food.price}}</span>
							<span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
						</div>
						<div class="cartcontrol-wrapper">
							<cartcontrol v-on:cart-add="addFood" :food="food" 
							></cartcontrol>
						</div>
						<transition name="fade">
							<div @click.stop.prevent="addFirst" class="buy" v-show="!food.count ||food.count === 0">加入购物车</div>
						</transition>
					</div>
					<split v-show="food.info"></split>
					<div class="info" v-show="food.info">
						<h1 class="title">商品信息</h1>
						<p class="text">{{food.info}}</p>
					</div>
					<split></split>
					<div class="rating">
						<h1 class="title">商品评价</h1>
						<ratingselect @select="selectRating" @toggle="toggleContent" :selectType="selectType" :onlyContent="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
						<div class="rating-wrapper">
							<ul v-show="food.ratings && food.ratings.length">
								<li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="rating-item">
									<div class="user">
										<span class="name">{{rating.username}}</span>
										<img class="avatar" :src="rating.avatar">
									</div>
									<div class="time">{{rating.rateTime | formatDate}}</div>
									<p class="text">
										<span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>
										<span >{{rating.text}}</span>
									</p>
								</li>
							</ul>
							<div class="no-rating"  v-show="!food.ratings || !food.ratings.length">
							</div>
						</div>
					</div>
				</div>
			</div>
		</transition>
	</div>
	
</template>

<script>
import BScroll from 'better-scroll';
import Vue from 'vue';
import cartcontrol from '../cartcontrol/cartcontrol.vue';
import split from '../split/split.vue';
import ratingselect from '../ratingselect/ratingselect.vue';
import {formatDate} from '../../assets/js/date.js';
	const ALL = 2;

	export default{
		name:'food',
		props: {
			food: {
				type: Object
			}
		},
		data(){
			return{
				showFlag: false,
				selectType: ALL,
				onlyContent: true,
				desc:{
					all:'全部',
					positive:'推荐',
					negative: '吐槽'
				}
			}
		},
		methods:{
			show(){
				this.showFlag = true;			
				this.$nextTick(() =>{
					if (!this.scroll) {
						this.scroll = new BScroll(this.$refs.food,{
							click: true
						})
					}else{
						this.scroll.refresh();
					}
				});
			},
			hide(){
				this.showFlag = false
			},
			addFirst(event){
				if (!event._constructed) {
		          return;
		        };
	            this.$emit('cart-add',event.target);
	        	Vue.set(this.food, 'count', 1);
			},
			addFood(target) {
	        	this.$emit('cart-add', target);
	      	},
			needShow(type, text) {
		        if (this.onlyContent && !text) {
		          return false;
		        }
		        if (this.selectType === ALL) {
		          return true;
		        } else {
		          return type === this.selectType;
		        }
     		},
     		selectRating(type) {
		        this.selectType = type;
		        this.$nextTick(() => {
		        	this.scroll.refresh();
      		  	});
	      	},
	        toggleContent() {
		        this.onlyContent = !this.onlyContent;
		        this.$nextTick(() => {
		          this.scroll.refresh();
		        });
		    },
		    
		},
		components:{
			cartcontrol,
			split,
			ratingselect
		},
		filters: {
	      	formatDate(time) {
		        let date = new Date(time);
		        return formatDate(date, 'yyyy-MM-dd hh:mm');
      			}
    		},

	}

</script>

<style lang="scss">
	.food{
		position: fixed;
		left: 0;
		top: 0;
		bottom: 48px;
		z-index: 30;
		width: 100%;
		background: #fff;
		&.move-enter-active,&.move-leave-active{
	        transition: all .3s;
	    }
	    &.move-enter,&.move-leave-to{
	    	transform: translate3d(100%,0,0);
	    }
	    .food-content{
	    	.image-header{
		    	position:relative;
		    	width: 100%;
		    	height: 0;
		    	padding-top: 100%;
		    	img{
		    		position:absolute;
		    		top: 0;
		    		left: 0;
		    		width: 100%;
		    		height: 100%;
		    	}
		    	.back{
		    		position:absolute;
		    		top: 10px;
		    		left: 0;
		    		.comeBack{
		    			width: 16px;
		    			height: 16px;
		    			background:url('comeBack.png') center center no-repeat;
		    			background-size: 16px 16px;
		    			padding:10px;
		    		}
		    	}
	    	}
	    	.content{
	    		padding:18px;
	    		position: relative;
	    		.title{
	    			line-height: 14px;
	    			margin-bottom: 8px;
	    			font-size: 14px;
	    			font-weight: 700;
	    			color: rgb(7,17,27);
	    		}
	    		.detail{
	    			margin-bottom: 18px;
	    			line-height: 10px;
	    			font-size: 0;
	    			.sell-count, .rating{
	    				font-size: 10px;
	    				color: rgb(147,153,159);
	    			}
	    			.sell-count{
	    				margin-right: 12px;
	    			}
	    		}
	    		.price{
					font-weight: 700;
					line-height: 24px;
					.now{
						margin-right: 8px;
						font-size: 14px;
						color: rgb(240,20,20);
					}
					.old{
						text-decoration: line-through;
						font-size: 10px;
						color: rgb(147,153,159);
					}
				}
				.cartcontrol-wrapper{
		    		position: absolute;
		    		right: 18px;
	    			bottom: 18px;
	    			}
		    	.buy{
		    		position:absolute;
		    		right: 18px;
		    		bottom:18px;
		    		z-index: 10;
		    		height: 24px;
		    		line-height: 24px;
		    		padding:0 12px;
		    		box-sizing: border-box;
		    		font-size: 10px;
		    		border-radius: 12px;
		    		color: #fff;
		    		background:rgb(0,160,220);
		    		&.fade-enter-active, &.fade-leave-active{
			        	transition: all .2s;
		    		}
			        &.fade-enter, &.fade-leave-to{
			        	opacity: 0;
			        	z-index: -1;
			        }
			          
		    	}
	    	}
	    	
	    }
	    .info{
	    	padding:18px;
	    	.title{
	    		line-height: 14px;
	    		margin-bottom: 6px;
	    		font-size: 14px;
	    		color: rgb(7,17,27);
	    	}
	    	.text{
	    		line-height: 16px;
	    		padding:0 8px;
	    		font-size: 12px;
	    		color: rgb(77,85,93);
	    	}
	    }
	    .rating{
	    	padding-top:18px;
	    	.title{
	    		line-height: 14px;
	    		font-size: 14px;
	    		color: rgb(7,17,27);
	    		margin-left: 18px;
	    	}
	    	.rating-wrapper{
	    		padding:0 18px;
	    		.rating-item{
	    			position:relative;
	    			padding:16px 0;
	    			border-bottom:1px solid rgba(7,17,27,.1);
	    			.user{
	    				position:absolute;
	    				right: 0;
	    				top: 16px;
	    				font-size: 0;
	    				line-height: 12px;
	    				.name{
	    					display: inline-block;
	    					vertical-align: top;
	    					margin-right: 6px;
	    					font-size: 10px;
	    					color: rgb(147,153,159);
	    				}
	    				.avatar{
	    					width: 12px;
	    					height: 12px;
	    					border-radius:50%;	
	    				}
	    			}
	    			.time{
	    				line-height: 12px;
	    				font-size: 10px;
	    				color: rgb(147,153,159);
	    				margin-bottom: 6px;
	    			}
	    			.text{
	    				line-height: 16px;
	    				font-size: 12px;
	    				color: rgb(7,17,27);
	    				display: inline-block;
	    				.icon-thumb_up, .icon-thumb_down{
	    					margin-right: 4px;
	    					line-height: 24px;
	    					font-size: 12px;
	    					width: 12px;
	    					height: 12px;
	    					background-size:12px 12px;
	    					display: inline-block;
	    				}
	    				.icon-thumb_up{
	    					background:url('1.png') center center no-repeat;
	    				}
	    				.icon-thumb_down{
	    					background:url('0.png') center center no-repeat;
	    				}
	    			}
	    		}
	    		.no-rating{
	    			padding: 16px 0;
	    			font-size: 12px;
	    			color: rgb(147,153,159);
	    		}
	    	}
	    }
	}
</style>