<template>
  <div class="cartcontrol">
    <transition name="move">
      <div class="cart-decrease" v-show="food.count>0" @click.stop.prevent="decreaseCart">
      </div>
    </transition>
    <transition name="count">
    <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
    </transition>
    <div class="cart-add" @click.stop.prevent="addCart"></div>
  </div>
</template>

<script>
  import Vue from 'vue'
  export default{
    props:{
      food:{
        type: Object
      }
    },
    methods:{
      addCart(event){
        if (!event._constructed) {
          return;
        }
        if (!this.food.count) {
          Vue.set(this.food, 'count', 1)
        }else{
          this.food.count++
        }
        this.$nextTick(() => {
           this.$emit('cart-add',event.target);
        })
       
      },
      decreaseCart(event){
        if (!event._constructed) {
          return;
        }
        if (this.food.count) {
          this.food.count--
        }
      }
    }

  }
</script>

<style lang="scss">
  .cartcontrol{
    .cart-decrease,.cart-count,.cart-add{
      display: inline-block;
      margin: 4px;
      width: 16px;
      height: 16px;
      vertical-align: top;
      text-align: center;
      background-size:16px 16px;
    }
    .cart-decrease{
      background: url('-.png') center center no-repeat;
      transform: rotate(180deg);
      &.move-enter-active,&.move-leave-active{
      transition: all .5s;
      }
      &.move-enter,&.move-leave-to{
        transform: rotate(360deg);
        opacity: 0;
        transform: translate3d(24px, 0, 0);
      }
    }
    .cart-count{
      font-size: 16px;
      color: rgb(147,153,19);
      &.count-enter-active,&.count-leave-active{
      transition: all 1s;
      }
      &.count-enter,&.count-leave-to{
        opacity: 0;
      }
    }
    .cart-add{
      background: url('+.png')center center no-repeat;
    }
  }
</style>