<template>
	<div class="goods">
		<div class="menu-wrapper" ref="menuWrapper">
			<ul>
				<li v-for="(item, index) in goods" class="menu-item" :class="{'current':currentIndex === index}"  @click="selectMenu(index)">
					<span class="text">
						<span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
					</span>
				</li>
			</ul>
		</div>
		<div class="foods-wrapper" ref="foodsWrapper">
			<ul>
				<li v-for="item in goods" class="food-list food-list-hook">
					<h1 class="title">{{item.name}}</h1>
					<ul>
						<li @click="selectFood(food)" v-for="food in item.foods" class="food-item">
							<div class="icon">
								<img :src="food.icon">
							</div>
							<div class="content">
								<h2 class="name">{{food.name}}</h2>
								<p class="desc">{{food.description}}</p>
								<div class="extra">
									<span class="count">月售{{food.sellCount}}份</span>
									<span>好评率{{food.rating}}</span>
								</div>
								<div class="price">
									<span class="now">￥{{food.price}}</span>
									<span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
								</div>
								<div class="cartcontrol-wrapper">
									<cartcontrol :food="food" v-on:cart-add="cartAdd"></cartcontrol>
								</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<shopcart  ref="shopcart" :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>	
		<food ref="food" v-on:cart-add="cartAdd" :food="selectedFood"></food>
	</div>
</template>

<script>
	import axios from 'axios';
	import BScroll from 'better-scroll';
	import shopcart from '../shopcart/shopcart.vue';
	import food from '../food/food.vue';
	import cartcontrol from '../cartcontrol/cartcontrol.vue';
	export default{
		name:'goods',
		data(){
			return{
				goods:[],
				selectedFood:{},
				listHeight: [],
			    scrollY: 0, //实时获取当前y轴的高度
			    clickEvent: false
			}
		},
		props:{
			seller:{
				type: Object
			}
		},
		 components: {
      		'shopcart': shopcart,
      		cartcontrol,
      		food
    	},
		 mounted () {
    		axios
	        .get('data.json')
	        .then (res => {
	        	this.goods = res.data.goods;
		        this.$nextTick(() => {
			       this._initScroll();
			       this._getHeight();
			  	});
	    	})
  		},
  		created() {
      		this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    	},
    	methods:{
    		_initScroll () {
		      //better-scroll的实现原理是监听了touchStart,touchend事件，所以阻止了默认的事件（preventDefault）
		      //所以在这里做点击的话，需要在初始化的时候传递属性click,派发一个点击事件
		      //在pc网页浏览模式下，点击事件是不会阻止的，所以可能会出现2次事件，所以为了避免2次，可以在绑定事件的时候把$event传递过去
		      this.lefts = new BScroll(this.$refs.menuWrapper, {
		        click: true
		      })
		      this.rights = new BScroll(this.$refs.foodsWrapper, {
		        click: true,
		        probeType: 3 //探针的效果，实时获取滚动高度

		      })
		      //rights这个对象监听事件，实时获取位置pos.y
		      this.rights.on('scroll', (pos) => {
		        this.scrollY = Math.abs(Math.round(pos.y))
		      })
		    },
		    _getHeight () {
		      let rightItems = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook')
		      let height = 0
		      this.listHeight.push(height)
		      for(let i = 0; i < rightItems.length; i++){
		        let item = rightItems[i]
		        height += item.clientHeight
		        this.listHeight.push(height)
		      }
		    },
		    selectMenu(index){
		      
		      //在better-scroll的派发事件的event和普通浏览器的点击事件event有个属性区别_constructed
		      //浏览器原生点击事件没有_constructed所以当时浏览器监听到该属性的时候return掉
		      if(!event._constructed){
		        return
		      }else{
		        let rightItems = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook')
		        let el = rightItems[index]
		        this.rights.scrollToElement(el, 300)
		      }
		    },
    		cartAdd(el){
    			this.$refs.shopcart.drop(el)
    		},
    		selectFood(food){
    			this.clickEvent = true
    			console.log("sasa")
    			if (!event._constructed) {
		          return;
		        }
    			this.selectedFood = food;
    			this.$refs.food.show()
    		}
    	},
    	computed:{
    		selectFoods(){
    			let foods= [];
    			this.goods.forEach((good) => {
    				good.foods.forEach((food) => {
    					if(food.count){
    						foods.push(food);
    					}
    				});
    			});
    			return foods
    		},
    		currentIndex () {
		      for(let i = 0; i < this.listHeight.length; i ++){
		        let height = this.listHeight[i]
		        let height2 = this.listHeight[i + 1]
		        //当height2不存在的时候，或者落在height和height2之间的时候，直接返回当前索引
		        //>=height，是因为一开始this.scrollY=0,height=0
		        if(!height2 || (this.scrollY >= height && this.scrollY < height2)){
		          return i
		        }
		        if(this.listHeight[this.listHeight.length - 1] - this.$refs.foodsWrapper.clientHeight <= this.scrollY){
		          if(this.clickTrue){
		            return this.currentNum
		          }else{
		            return (this.listHeight.length - 1)
		          }
		        }
		      }
		      //如果this.listHeight没有的话，就返回0
		      return 0
		    }
    	},

	}
	
</script>

<style lang="scss">
@mixin bg-image($url) {
    background-image: url($url + "@2x.png");
    @media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3) {
        background-image: url($url + "@3x.png");
    }
}
	.goods{
		position: absolute;
		display: flex;
		top: 174px;
		bottom: 46px;
		width: 100%;
		overflow: hidden;
		.menu-wrapper{
			flex:  0 0 80px;
			width: 80px;
			background: #f3f5f7;
			.menu-item{
				display: table;
				width: 56px;
				height: 54px;
				padding: 0 12px;
				line-height: 14px;
				.icon{
					display: inline-block;
					vertical-align: top;
					width: 12px;
					height: 12px;
					margin-right: 2px;
					background-size: 12px 12px;
					background-repeat:no-repeat;
					&.decrease{
						@include bg-image('decrease_3');
					}
					&.discount{
						@include bg-image('discount_3');
					}
		            &.guarantee{
		            	@include bg-image('guarantee_3');
		            }
		            &.invoice{
		            	@include bg-image('invoice_3');
		            }
		            &.special{
		            	@include bg-image('special_3');
		            }

				}
				.text{
					display: table-cell;
					width: 56px;
					vertical-align: middle;
					font-size: 12px;
					border-bottom: 1px solid rgba(7,17,27,.1);
					font-size: 12px;
				}
			}
			.current{
				background:#fff;
			}
		}
		.foods-wrapper{
			flex: 1;
			.title{
				padding-left: 14px;
				height: 26px;
				line-height: 26px;
				border-left:2px solid #d9dde1;
				font-size: 12px;
				color:rgb(147,153,159);
				background:#f3f5f7;
			}
			.food-item{
				position:relative;
				display: flex;
				margin: 18px;
				padding-bottom: 18px;
				border-bottom: 1px solid rgba(7,17,27,.1);
				&:last-child{
					margin-bottom:0;
					border:0;
				}
				.icon{
					flex: 0 0 57px;
					margin-right: 10px;
					img{
						width: 57px;
						height: 57px;
					}
				}
				.content{
					flex: 1;
					.name{
						margin: 2px 0 8px 0;
						height: 14px;
						line-height: 14px;
						font-size: 14px;
						color: rgb(7,17,27);
					}
					.desc, .extra{
						line-height: 10px;
						font-size: 10px;
						color:rgb(147,153,159);
					}
					.desc{
						line-height: 12px;
						margin-bottom: 8px;
					}
					.extra{
						.count{
							margin-right: 12px;
						}
					}
					.price{
						font-weight: 700;
						line-height: 24px;
						.now{
							margin-right: 8px;
							font-size: 14px;
							color: rgb(240,20,20);
						}
						.old{
							text-decoration: line-through;
							font-size: 10px;
							color: rgb(147,153,159);
						}
					}
					.cartcontrol-wrapper{
						position:absolute;
						right: 0;
						bottom:6px;
					}
				}
			}
		}
	}
</style>